@import "tailwindcss";
@import "tw-animate-css";

@custom-variant dark (&:is(.dark *));

:root {
  /* Clinical aesthetic: white base with light blue accents for medical trust */
  --background: oklch(0.99 0.001 230);
  --foreground: oklch(0.15 0.01 230);
  --card: oklch(1 0 0);
  --card-foreground: oklch(0.15 0.01 230);
  --popover: oklch(1 0 0);
  --popover-foreground: oklch(0.15 0.01 230);

  /* Primary: Professional medical blue */
  --primary: oklch(0.42 0.12 230);
  --primary-foreground: oklch(0.99 0.001 230);

  /* Secondary: Light clinical blue */
  --secondary: oklch(0.88 0.04 230);
  --secondary-foreground: oklch(0.15 0.01 230);

  --muted: oklch(0.93 0.01 230);
  --muted-foreground: oklch(0.45 0.02 230);

  /* Accent: Teal for medical data highlighting */
  --accent: oklch(0.55 0.1 190);
  --accent-foreground: oklch(0.99 0.001 230);

  --destructive: oklch(0.577 0.245 27.325);
  --destructive-foreground: oklch(0.99 0.001 230);
  --border: oklch(0.94 0.01 230);
  --input: oklch(0.97 0.01 230);
  --ring: oklch(0.42 0.12 230);

  --chart-1: oklch(0.42 0.12 230);
  --chart-2: oklch(0.55 0.1 190);
  --chart-3: oklch(0.65 0.08 170);
  --chart-4: oklch(0.72 0.06 200);
  --chart-5: oklch(0.8 0.04 220);

  --radius: 0.625rem;
  --sidebar: oklch(0.99 0.001 230);
  --sidebar-foreground: oklch(0.15 0.01 230);
  --sidebar-primary: oklch(0.42 0.12 230);
  --sidebar-primary-foreground: oklch(0.99 0.001 230);
  --sidebar-accent: oklch(0.88 0.04 230);
  --sidebar-accent-foreground: oklch(0.15 0.01 230);
  --sidebar-border: oklch(0.94 0.01 230);
  --sidebar-ring: oklch(0.42 0.12 230);
}

.dark {
  /* Dark mode: slate background with clinical blue accents for reduced eye strain */
  --background: oklch(0.12 0.01 230);
  --foreground: oklch(0.96 0.001 230);
  --card: oklch(0.18 0.01 230);
  --card-foreground: oklch(0.96 0.001 230);
  --popover: oklch(0.18 0.01 230);
  --popover-foreground: oklch(0.96 0.001 230);

  /* Primary: Bright clinical blue for dark mode */
  --primary: oklch(0.68 0.12 230);
  --primary-foreground: oklch(0.12 0.01 230);

  /* Secondary: Darker light blue */
  --secondary: oklch(0.32 0.05 230);
  --secondary-foreground: oklch(0.96 0.001 230);

  --muted: oklch(0.32 0.02 230);
  --muted-foreground: oklch(0.7 0.02 230);

  /* Accent: Bright teal */
  --accent: oklch(0.7 0.12 190);
  --accent-foreground: oklch(0.12 0.01 230);

  --destructive: oklch(0.65 0.2 30);
  --destructive-foreground: oklch(0.96 0.001 230);
  --border: oklch(0.27 0.01 230);
  --input: oklch(0.24 0.01 230);
  --ring: oklch(0.68 0.12 230);

  --chart-1: oklch(0.68 0.12 230);
  --chart-2: oklch(0.7 0.12 190);
  --chart-3: oklch(0.72 0.1 170);
  --chart-4: oklch(0.65 0.08 200);
  --chart-5: oklch(0.6 0.06 220);

  --sidebar: oklch(0.15 0.01 230);
  --sidebar-foreground: oklch(0.96 0.001 230);
  --sidebar-primary: oklch(0.68 0.12 230);
  --sidebar-primary-foreground: oklch(0.15 0.01 230);
  --sidebar-accent: oklch(0.32 0.05 230);
  --sidebar-accent-foreground: oklch(0.96 0.001 230);
  --sidebar-border: oklch(0.27 0.01 230);
  --sidebar-ring: oklch(0.68 0.12 230);
}

@theme inline {
  --font-sans: "Geist", "Geist Fallback";
  --font-mono: "Geist Mono", "Geist Mono Fallback";
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --color-card: var(--card);
  --color-card-foreground: var(--card-foreground);
  --color-popover: var(--popover);
  --color-popover-foreground: var(--popover-foreground);
  --color-primary: var(--primary);
  --color-primary-foreground: var(--primary-foreground);
  --color-secondary: var(--secondary);
  --color-secondary-foreground: var(--secondary-foreground);
  --color-muted: var(--muted);
  --color-muted-foreground: var(--muted-foreground);
  --color-accent: var(--accent);
  --color-accent-foreground: var(--accent-foreground);
  --color-destructive: var(--destructive);
  --color-destructive-foreground: var(--destructive-foreground);
  --color-border: var(--border);
  --color-input: var(--input);
  --color-ring: var(--ring);
  --color-chart-1: var(--chart-1);
  --color-chart-2: var(--chart-2);
  --color-chart-3: var(--chart-3);
  --color-chart-4: var(--chart-4);
  --color-chart-5: var(--chart-5);
  --radius-sm: calc(var(--radius) - 4px);
  --radius-md: calc(var(--radius) - 2px);
  --radius-lg: var(--radius);
  --radius-xl: calc(var(--radius) + 4px);
  --color-sidebar: var(--sidebar);
  --color-sidebar-foreground: var(--sidebar-foreground);
  --color-sidebar-primary: var(--sidebar-primary);
  --color-sidebar-primary-foreground: var(--sidebar-primary-foreground);
  --color-sidebar-accent: var(--sidebar-accent);
  --color-sidebar-accent-foreground: var(--sidebar-accent-foreground);
  --color-sidebar-border: var(--sidebar-border);
  --color-sidebar-ring: var(--sidebar-ring);
}

@layer base {
  * {
    @apply border-border outline-ring/50;
  }
  body {
    @apply bg-background text-foreground;
  }
}
